






























































































































































































import { Component, Model, Prop, Vue } from 'vue-property-decorator';
import PrivacyPolicyDialog from '@/components/PrivacyPolicyDialog.vue';
import emailjs from 'emailjs-com';
import { emailValidator } from '@/services/RemoteService';

@Component({
  components: {
    'privacy-policy-dialog': PrivacyPolicyDialog
  }
})
export default class ContactDialog extends Vue {
  @Model('dialog', Boolean) dialog!: boolean;
  @Prop(Boolean) isMessage!: boolean;

  privacyDialog = false;
  isValid = true;
  name = '';
  phone = '';
  email = '';
  subject = '';
  messageText = '';
  selectDays = [];
  selectHours = [];
  agreement = false;
  isAgreementSeen = false;

  warningEmail() {
    if (this.$store.getters.isPortuguese) {
      return 'Email inválido';
    } else {
      return 'Invalid Email';
    }
  }

  warningPhone() {
    if (this.$store.getters.isPortuguese) {
      return 'Número de Telemóvel inválido';
    } else {
      return 'Invalid phone number';
    }
  }

  fullName() {
    if (this.$store.getters.isPortuguese) {
      return 'Nome Completo';
    } else {
      return 'Full Name';
    }
  }

  phoneNumber() {
    if (this.$store.getters.isPortuguese) {
      return 'Telemóvel';
    } else {
      return 'Mobile Phone';
    }
  }

  warning() {
    if (this.$store.getters.isPortuguese) {
      return 'Campo de preenchimento obrigatório';
    } else {
      return 'Required field';
    }
  }

  warningText() {
    if (this.$store.getters.isPortuguese) {
      return 'Campo de preenchimento obrigatório com limite de caractéres';
    } else {
      return 'Required field with character limit';
    }
  }

  warningPrivacy() {
    if (this.$store.getters.isPortuguese) {
      return 'Tem que concordar para continuar';
    } else {
      return 'Agree to continue';
    }
  }

  hintLabel() {
    if (this.$store.getters.isPortuguese) {
      return 'Escolha o(s) dia(s) da semana e a(s) hora(s) mais convinientes para si (sujeito a confirmação)';
    } else {
      return 'Choose the most convenient hours and days of the week (Confirmation Required)';
    }
  }

  weekDayLabel() {
    if (this.$store.getters.isPortuguese) {
      return 'Dias da Semana';
    } else {
      return 'Week Days';
    }
  }

  hourLabel() {
    if (this.$store.getters.isPortuguese) {
      return 'Horas';
    } else {
      return 'Hours';
    }
  }

  textFiledLabel() {
    if (this.$store.getters.isPortuguese) {
      if (this.isMessage) {
        return 'Mensagem';
      } else {
        return 'Motivo da Consulta';
      }
    } else {
      if (this.isMessage) {
        return 'Message';
      } else {
        return 'Appointment reason';
      }
    }
  }

  clickOutOfBox() {
    if (this.$store.getters.isPortuguese) {
      return 'Clique fora da caixa para confirmar a seleção';
    } else {
      return 'Click out of the box to confirm your selection';
    }
  }

  days() {
    if (this.$store.getters.isPortuguese) {
      return [
        'Segunda-feira',
        'Terça-feira',
        'Quarta-feira',
        'Quinta-feira',
        'Sexta-feira'
      ];
    } else {
      return ['Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday'];
    }
  }

  hours = ['09:00-12:00', '12:00-15:00', '15:00-18:00'];

  dialogTitle() {
    if (this.$store.getters.isPortuguese) {
      if (!this.isMessage) {
        return 'Pedido de Agendamento de Consulta';
      } else {
        return 'Pedido de Contacto';
      }
    } else {
      if (this.$store.getters.isEnglish) {
        if (!this.isMessage) {
          return 'Ask for an Appointment';
        } else {
          return 'Message Us';
        }
      }
    }
  }

  closeTooltip() {
    if (this.$store.getters.isPortuguese) {
      return 'Fechar Formulário';
    } else {
      return 'Close Form';
    }
  }

  closeDialog() {
    this.$emit('close-dialog');
  }

  privacyLabel1() {
    if (this.$store.getters.isPortuguese) {
      return 'Declaro que li e concordo com a';
    } else {
      return 'I acknowledge that I have read and agree with the';
    }
  }

  privacyLabel2() {
    if (this.$store.getters.isPortuguese) {
      return 'Política de Privacidade.';
    } else {
      return 'Privacy Policy.';
    }
  }

  openPrivacyPolicy() {
    this.agreementSeen();
    this.privacyDialog = true;
  }

  agreementSeen() {
    this.isAgreementSeen = true;
  }

  openFirstPrivacyPolicy() {
    if (!this.isAgreementSeen) this.privacyDialog = true;
    this.agreementSeen();
  }

  onClosePrivacyDialog() {
    this.privacyDialog = false;
  }

  cleanFormLabel() {
    if (this.$store.getters.isPortuguese) {
      return 'Limpar dados';
    } else {
      return 'Clean Form';
    }
  }

  submitFormLabel() {
    if (this.$store.getters.isPortuguese) {
      return 'Enviar';
    } else {
      return 'Submit';
    }
  }

  async onSubmit() {
    let templateParams = {};
    let template = '';
    if (this.isMessage) {
      template = 'contacto';
      templateParams = {
        fromName: this.name,
        fromEmail: this.email,
        fromPhone: this.phone,
        messageSubject: this.dialogTitle(),
        messageText: this.messageText
      };
    } else {
      template = 'agendamento';
      templateParams = {
        fromName: this.name,
        fromEmail: this.email,
        fromPhone: this.phone,
        selectedDays: this.selectDays,
        selectedHours: '/ ' + this.selectHours,
        messageSubject: this.dialogTitle(),
        messageText: this.messageText
      };
    }

    const response = await emailValidator(this.email);

    if (response.status === 200) {
      response.data.status === 1
        ? this.sendEmail(templateParams, template)
        : this.$emit('invalid-snackbar');
    } else {
      this.sendEmail(templateParams, template);
    }
  }

  sendEmail(templateParams: {}, template: string) {
    const EMAILJS_USER_ID = 'user_0P8qpeCBKoDuSLGXpwO3x';
    const EMALJS_SERVICE_ID = 'cmdmiratejo';
    emailjs
      .send(EMALJS_SERVICE_ID, template, templateParams, EMAILJS_USER_ID)

      .then(
        result => {
          console.log('SUCCESS!', result.status, result.text);
          this.$emit('success-snackbar');
          this.closeDialog();
        },
        error => {
          this.$emit('error-snackbar');
          console.log('FAILED', error);
        }
      );
  }
}
