




























import { Component, Vue } from 'vue-property-decorator';
import FooterComponent from '@/components/FooterComponent.vue';
import TeamComponent from '@/components/about/TeamComponent.vue';
@Component({
  components: {
    TeamComponent,
    FooterComponent
  }
})
export default class HomeComponent extends Vue {
  alertText() {
    if (this.$store.getters.isPortuguese) {
      return 'Peça já a sua avaliação grátis atrabés de um pedido de contacto!';
    } else {
      return 'The Miratejo Dental Clinic will be open to the public on September 1st, 2020';
    }
  }
  appTitle() {
    return 'CLÍNICA DE MEDICINA DENTÁRIA DO MIRATEJO';
  }
  slogan() {
    if (this.$store.getters.isPortuguese) {
      return 'A sua <strong>SAÚDE ORAL</strong> é a nossa prioridade!';
    } else {
      return 'Your <strong>ORAL HEALTH</strong> is our priority!';
    }
  }
  alignment = 'end';
  position = 'center';
}
