import Vue from 'vue';
import Vuex from 'vuex';

interface State {
  english: boolean;
  portuguese: boolean;
  treatment: number;
  appTitle: string;
}

const state: State = {
  english: false,
  portuguese: true,
  treatment: 0,
  appTitle: 'CLÍNICA DE MEDICINA DENTÁRIA DO MIRATEJO'
};

Vue.use(Vuex);
Vue.config.devtools = true;

export default new Vuex.Store({
  state: state,
  mutations: {
    setEnglish(state) {
      state.english = true;
      state.portuguese = false;
    },
    setPortuguese(state) {
      state.portuguese = true;
      state.english = false;
    },
    setTreatmentSlide(state, treatment: number) {
      state.treatment = treatment;
    }
  },
  actions: {
    setEnglish(state) {
      state.commit('setEnglish');
    },
    setPortuguese(state) {
      state.commit('setPortuguese');
    },
    setTreatmentSlide({ commit }, treatment) {
      commit('setTreatmentSlide', treatment);
    }
  },
  modules: {},

  getters: {
    isPortuguese(state): boolean {
      return state.portuguese;
    },
    isEnglish(state): boolean {
      return state.english;
    },
    getTreatmentSlide(state): number {
      return state.treatment;
    },
    getAppTitle(state): string {
      return state.appTitle;
    }
  }
});
