import Vue from 'vue';
import Router from 'vue-router';
import Home from './views/Home.vue';
import Treatments from '@/views/Treatments.vue';
import AboutClinic from '@/views/AboutClinic.vue';
import AboutTeam from '@/views/AboutTeam.vue';

Vue.use(Router);

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      meta: {
        title: process.env.VUE_APP_NAME + ' - Home',
        requiredAuth: 'None'
      }
    },
    {
      path: '/about-clinic',
      name: 'about-clinic',
      component: AboutClinic,
      meta: {
        title: process.env.VUE_APP_NAME + ' - About Clinic',
        requiredAuth: 'None'
      }
    },
    {
      path: '/about-team',
      name: 'about-team',
      component: AboutTeam,
      meta: {
        title: process.env.VUE_APP_NAME + ' - About Team',
        requiredAuth: 'None'
      }
    },
    {
      path: '/treatments',
      name: 'treatments',
      component: Treatments,
      meta: {
        title: process.env.VUE_APP_NAME + ' - Treatments',
        requiredAuth: 'None'
      }
    }
  ]
});

router.beforeEach(async (to, from, next) => {
  if (to.meta.requiredAuth == 'None') {
    next();
  } else {
    next('/');
  }
});

router.afterEach(async to => {
  document.title = to.meta.title;
});

export default router;
