




























import { Component, Vue } from 'vue-property-decorator';
@Component
export default class FabButton extends Vue {
  offset = false;
  created() {
    window.addEventListener('scroll', this.scrolled);
  }
  scrolled() {
    this.offset = window.pageYOffset > 250;
  }

  scrollUp() {
    this.$vuetify.goTo(0);
  }
}
