
























































































































































































import { Component, Vue } from 'vue-property-decorator';
@Component
export default class ContactsComponent extends Vue {
  alignment = 'center';

  contactsLabel() {
    if (this.$store.getters.isPortuguese) {
      return 'CONTACTOS';
    } else {
      return 'CONTACTS';
    }
  }

  getEmail() {
    return 'geral@cmdmiratejo.org';
  }
}
