






















































































































import { Component, Prop, Vue } from 'vue-property-decorator';
import TreatmentDialog from '@/components/treatments/TreatmentDialog.vue';
@Component({
  components: { TreatmentDialog }
})
export default class TreatmentComponents extends Vue {
  @Prop(String) treatment!: string;
  @Prop(String) text1!: string;
  @Prop(String) text2!: string;
  @Prop(String) cols1!: string;
  @Prop(String) cols2!: string;
  @Prop(String) color!: string;
  @Prop(String) image!: string;
  @Prop(Boolean) action!: boolean;
  @Prop({ type: Array }) items!: never[];
  dialog = false;
  source = '';

  alignment = 'center';

  onVideo(source: string) {
    this.source = source;
    this.dialog = true;
  }

  onCloseDialog() {
    this.dialog = false;
  }
}
